  setAnimations(obj, anim, sno) {
    var that = this
    for (var i = anim.length - 1; i >= 0; i--) {
      var _anim = anim[i]
      var duration = _anim.duration || 2
      var from = _anim.from
      var to = _anim.to
      var _repeat = _anim.repeat
      var _delay = _anim.delay
      var _enter = _anim.enter != undefined ? _anim.enter : false
      // console.log(_enter)

      if (_anim.frameflag) {
        // 帧动画
        var frameslength = _anim.frameslength
        that.on('progress', (progress) => {
          var frameProgress = (progress - _delay) / duration
          var index = Math.floor(frameProgress * frameslength)
          if (index < frameslength && index >= 0) {
            // const frame = frames[index]
            obj.gotoAndStop(index)
          }
        })
      } else {
        var tw
        var scaleF = _anim.scaleF
        if (scaleF) {
          var _obj = obj.scale
        } else {
          var _obj = obj
        }
        if (from && to) {
          to.ease = Power0.easeInOut
          tw = new TweenMax.fromTo(_obj, duration, from, to)
        } else {
          if (from) {
            from.ease = Power0.easeInOut
            tw = new TweenMax.from(_obj, duration, from)
          } else if (to) {
            // if (to.ease == undefined) {
            //     to.ease = Power0.easeInOut
            // }
            if (_repeat == -1) {
              to.repeat = _repeat
            }

            tw = new TweenMax.to(_obj, duration, to)
          }
        }
        if (tw) {
          var tl = new TimelineMax({ delay: _delay })
          tl.add(tw, 0)
          tl.play()
          if (_repeat !== -1) {
            if (_enter) {
              enterTimeline.add(tl, 0)
            } else {
              timeline.add(tl, 0)
            }
          }
          // console.log(that.timeline.totalDuration())
        }
      }
    }
  }
  initSpine(res, neat) {
    var mspine = new PIXI.spine.Spine(res)
    var localRect1 = mspine.getLocalBounds()
    mspine.position.set(-localRect1.x, -localRect1.y)
    if (!neat) {
      mspine.stateData.setMix('swim', 'eat', 0.1)
      mspine.stateData.setMix('eat', 'swim', 0.1)
    }

    return mspine
  }
