      function splitText(
        txt,
        textcontainer,
        fontface,
        fontsize,
        letterspacing,
        lineheight
      ) {
        var _array = txt.split('')
        var letterS = -fontsize
        for (var i = 0; i < _array.length; i++) {
          ;(function (k) {
            var dword = 0
            var dline = 0
            var _fontsize = fontsize
            var w = _array[k]
            if (w == '7' || w == '9' || w == '%') {
              _fontsize = 62
              if (w == '9' || w == '%') {
                dword = -25
              } else {
                dword = -8
              }
              dline = -10
            } else {
              if (w == '都') {
                dword = 13
                dline = 0
              } else {
                dword = 0
                dline = 0
              }
            }
            if (w == '4' || w == '0' || w == '种') {
              dword = -20
              dline = 0
            }
            var letterspacings = (letterspacing * _fontsize) / 1000
            // console.log(_fontsize)
            letterS = letterS + _fontsize + letterspacings + dword
            var bitmapFontText = new PIXI.extras.BitmapText(_array[k], {
              font: _fontsize + 'px ' + fontface,
              align: 'left',
            })
            bitmapFontText.x = letterS
            bitmapFontText.y = lineheight + dline
            textcontainer.addChild(bitmapFontText)
          })(i)
        }
      }

      function splitText2(
        txt,
        textcontainer,
        fontface,
        fontsize,
        letterspacing,
        lineheight
      ) {
        var _array = txt.split('')
        var letterS = -fontsize
        for (var i = 0; i < _array.length; i++) {
          ;(function (k) {
            var dword = 0
            var dline = 0
            var _fontsize = fontsize
            var w = _array[k]
            if (w == '海') {
              dword = -17
            }
            if (w == '5' || w == '.' || w == '2' || w == '万' || w == '亿') {
              fontface = 'text-font2'
              _fontsize = 68
              dline = 10
              if (w == '5' || w == '万') dword = -30
              if (w == '.') dword = -15
              if (w == '2') dword = -35
            } else {
              fontface = 'text-font1'
            }
            if (w == '个') dword = +15
            var letterspacings = (letterspacing * _fontsize) / 1000

            letterS = letterS + _fontsize + letterspacings + dword
            var bitmapFontText = new PIXI.extras.BitmapText(_array[k], {
              font: _fontsize + 'px ' + fontface,
              align: 'left',
            })
            bitmapFontText.x = letterS
            bitmapFontText.y = lineheight + dline
            textcontainer.addChild(bitmapFontText)
          })(i)
        }
      }

      function splitLine2(
        text,
        textcontainer,
        fontface,
        fontsize,
        letterspacing,
        lineheight
      ) {
        var array = text.split(/\n/)
        var lineheightS = 0
        for (var i = 0; i < array.length; i++) {
          ;(function (k) {
            splitText2(
              array[k],
              textcontainer,
              fontface,
              fontsize,
              letterspacing,
              lineheightS
            )
            lineheightS += lineheight
          })(i)
        }
      }

